* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

button {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: #330d1f;
  cursor: pointer;
}

.login {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background: #701f1f;
  background-image: url("https://cdn.pixabay.com/photo/2016/05/24/13/29/olive-oil-1412361_960_720.jpg");
  display: flex;
}

.login .loginContainer {
  padding: 60px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgb(11, 28, 209) 0%,
    rgba(98, 74, 141, 0.9) 59%,
    rgba(17, 52, 230, 0.9) 100%
  );
  background: radial-gradient(
    ellipse at right bottom,
    rgb(4, 6, 26) 0%,
    rgba(98, 74, 141, 0.9) 59%,
    rgba(17, 52, 230, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(172, 153, 207, 0.8);
}

.login .loginContainer body {
  color: rgb(235, 231, 231);
  margin: 14px 0;
  display: block;
  font-size: 69px;
  line-height: 1;
  text-align: center;
}

.login .loginContainer label {
  color: rgb(235, 231, 231);
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.login .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(12, 0, 0, 0.1);
  color: rgb(7, 7, 7);
  letter-spacing: 1px;
}

.login .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}

.login .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}

.login .loginContainer .btnContainer p span {
  color: yellow;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}

.login .loginContainer .btnContainer p span:hover {
  color: red;
}

.login .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}

.HomePage {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(22, 24, 47, 1) 0%,
    rgba(38, 20, 72, 0.9) 59%,
    rgba(17, 27, 75, 0.9) 100%
  );
}

.HomePage nav {
  background: #603bbb;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.HomePage nav h2 {
  color: #fff;
}

.HomePage nav button {
  width: 86px;
  padding: 8px 0;
  background: #291a46;
}